import React, { useEffect, useRef } from "react";
import { motion } from "framer-motion";
import "particles.js"; // глобально
const Particles = () => {
  const ref = useRef();
  useEffect(() => {
    window.particlesJS &&
      window.particlesJS("particles-js", {
        particles: {
          number: { value: 65 },
          size: { value: 3 },
          color: { value: "#00f2fe" },
          line_linked: { enable: true, distance: 105, color: "#c471ed", opacity: 0.34, width: 1 }
        },
        interactivity: {
          events: { onhover: { enable: true, mode: "repulse" }, onclick: { enable: true, mode: "push" } }
        }
      });
  }, []);
  return <div id="particles-js" ref={ref} style={{
    position: "absolute",
    inset: 0,
    zIndex: 0,
    width: "100vw",
    height: "100vh",
    pointerEvents: "none"
  }} />;
};
const Hero = () => (
  <header style={{ position: "relative", height: "100vh", overflow: "hidden" }}>
    <Particles />
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ delay: .4, duration: 1 }}
      style={{
        position: "absolute",
        inset: 0,
        zIndex: 2,
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        textAlign: "center",
        pointerEvents: "auto"
      }}
    >
      <img src="https://i.postimg.cc/T2NNpFx1/photo-5211136734690145953-x.jpg" alt="WaterDrop logo"
        style={{ width: 85, marginBottom: 12, borderRadius: "20px", background: "#fff3" }} />
      <h1 style={{ fontSize: "clamp(2rem,6vw,4rem)", fontWeight: 900, margin: 0, marginBottom: 6 }}>
        WaterDrop Portfolio
      </h1>
      <p style={{
        fontSize: "clamp(1rem,2.5vw,1.5rem)", color: "#eee",
        marginBottom: "2.2rem"
      }}>
        Современный WOW-фон, калькуляторы услуг, AI-боты, генерация креативных идей
      </p>
      <a href="#calculator" className="btn">Рассчитать проект</a>
    </motion.div>
  </header>
);

export default Hero;
