import React from "react";
const works = [
  {
    title: "WOW-Лендинг",
    img: "https://i.postimg.cc/tCLrz6YH/photo-5211136734690145957-y.jpg",
    desc: "Продающая страница для увеличения заявок. Эффекты, видео, анимация."
  },
  {
    title: "Сайт-визитка",
    img: "https://i.postimg.cc/s2Jwy0KV/photo-5211136734690145958-y.jpg",
    desc: "Мини-презентация компании. Всё лаконично и понятно."
  }
];
const ProjectGallery = () => (
  <section>
    <h2 className="section-title">Примеры работ</h2>
    <div style={{
      display: "grid", gap: 22, margin: "auto", maxWidth: 950,
      gridTemplateColumns: "repeat(auto-fit,minmax(270px,1fr))"
    }}>
      {works.map((w, i) => (
        <div key={i} className="glass-card" style={{ padding: 16 }}>
          <img src={w.img} alt={w.title} style={{ width: "100%", borderRadius: "8px" }} />
          <div style={{ fontWeight: 600, margin: "15px 0 4px 0" }}>{w.title}</div>
          <div style={{ color: "#656" }}>{w.desc}</div>
        </div>
      ))}
    </div>
  </section>
);

export default ProjectGallery;
