import React, { useState } from "react";
import { calcScenarios, extras } from "../data/calcOptions";

const CostCalculator = () => {
  const [selected, setSelected] = useState(null);
  const [checks, setChecks] = useState([]);

  const price =
    (selected ? calcScenarios.find(b => b.key === selected).price : 0) +
    checks.reduce((s, k) => s + (extras.find(i => i.key === k)?.price || 0), 0);

  return (
    <section id="calculator">
      <h2 className="section-title">Рассчитайте стоимость вашего проекта</h2>
      <p className="section-subtitle">
        Выберите тип сайта, доп. функции и сразу увидите итоговую цену.<br />
        Всё понятно обычному человеку!
      </p>
      <div style={{
        display: "grid", gap: 16, margin: "24px auto", maxWidth: 1100,
        gridTemplateColumns: "repeat(auto-fit,minmax(270px,1fr))"
      }}>
        {calcScenarios.map(({ key, label, desc, price: pr }) => (
          <div key={key}
            className="glass-card"
            style={{
              padding: 18,
              outline: selected === key ? "3px solid #00f2fe" : "none",
              cursor: "pointer",
              minHeight: 160,
              transition: 'outline 0.2s'
            }}
            onClick={() => setSelected(key)}
          >
            <div style={{ fontSize: 22, fontWeight: 600 }}>{label}</div>
            <div style={{ fontSize: 15, color: "#555", margin: "10px 0 4px 0" }}>{desc}</div>
            <div style={{ color: "#00f2fe", fontWeight: 800 }}>{pr.toLocaleString()} ₽</div>
          </div>
        ))}
      </div>
      {selected && <>
        <div style={{
          display: "grid", gridTemplateColumns: "repeat(auto-fit, minmax(220px,1fr))",
          gap: 12, margin: "15px 0 12px 0"
        }}>
          {extras.map(e => (
            <label key={e.key} className="glass-card" style={{
              padding: 11, display: "flex", alignItems: "center", userSelect: "none"
            }}>
              <input
                type="checkbox"
                checked={checks.includes(e.key)}
                onChange={() =>
                  setChecks(cs => cs.includes(e.key)
                    ? cs.filter(x => x !== e.key)
                    : [...cs, e.key]
                  )}
                style={{ marginRight: 8 }}
              />
              {e.label}
              <span style={{ color: "#c471ed", marginLeft: "auto", fontSize: "15px", fontWeight: 700 }}>
                +{e.price.toLocaleString()}₽
              </span>
            </label>
          ))}
        </div>
        <div className="glass-card" style={{
          margin: "20px auto", maxWidth: 340, padding: 22, textAlign: "center"
        }}>
          <div style={{ color: "#007cf8", fontSize: 21 }}>
            <b>Итого:</b> {price.toLocaleString()} ₽
          </div>
          <div style={{ color: "#888", fontSize: 14, margin: 7 }}>Цена — ориентировочная, под запрос клиента всегда индивидуальная!</div>
        </div>
      </>}
    </section>
  );
};

export default CostCalculator;
