import React, { useState } from "react";
import ideas, { lifehacks } from "../data/ideasList";
import { motion, AnimatePresence } from "framer-motion";

const randomItems = (arr, n) => arr.sort(() => 0.5 - Math.random()).slice(0, n);

const IdeaGenerator = () => {
  const [cards, setCards] = useState([]);
  const [tip, setTip] = useState("");

  const handleGenerate = (e) => {
    e.preventDefault();
    setCards(randomItems(ideas, 3));
    setTip(randomItems(lifehacks, 1)[0]);
  };

  return (
    <section id="ideas">
      <h2 className="section-title">Генератор идей + подарок</h2>
      <p className="section-subtitle">Кликайте и получайте свежие идеи для сайта и один секретный WOW‑лайфхак!</p>
      <form onSubmit={handleGenerate} style={{ textAlign: "center", marginTop: "1rem" }}>
        <button type="submit" className="btn">Сгенерировать идеи!</button>
      </form>
      <div style={{
        display: "grid",
        gridTemplateColumns: "repeat(auto-fit,minmax(220px,1fr))",
        gap: "1.2rem",
        marginTop: "1.4rem"
      }}>
        <AnimatePresence>
          {cards.map((c) => (
            <motion.div key={c}
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: .7, opacity: 0 }}
              transition={{ duration: .28 }}
              className="glass-card"
              style={{ padding: "1.25rem", fontSize: "1.04rem", minHeight: 90 }}
            >
              {c}
            </motion.div>
          ))}
        </AnimatePresence>
      </div>
      {tip && (
        <div className="glass-card" style={{ margin: "1.6rem auto 0 auto", maxWidth: 410, fontSize: "1.04rem", background: "#c471ed22" }}>
          🎁 <b>Сюрприз лайфхак:</b> {tip}
        </div>
      )}
    </section>
  );
};

export default IdeaGenerator;
