import React, { useEffect, useState } from "react";
import { motion } from "framer-motion";

const openChat = () => {
  if (window.Chatbase) {
    window.Chatbase.open();
  }
};

const ChatbotButton = () => {
  const [ready, setReady] = useState(false);
  useEffect(() => {
    const timer = setInterval(() => {
      if ("Chatbase" in window) {
        setReady(true);
        clearInterval(timer);
      }
    }, 1000);
    return () => clearInterval(timer);
  }, []);
  if (!ready) return null;
  return (
    <motion.button
      onClick={openChat}
      aria-label="Открыть чат-бота"
      whileHover={{ scale: 1.09 }}
      whileTap={{ scale: 0.97 }}
      style={{
        position: "fixed",
        bottom: 20,
        right: 20,
        zIndex: 1002,
        border: "none",
        borderRadius: "50%",
        width: 64, height: 64,
        cursor: "pointer",
        background: "linear-gradient(135deg,#12c2e9,#c471ed,#f64f59)",
        boxShadow: "0 4px 12px #4facfe72"
      }}
    >
      <span role="img" aria-label="Bot" style={{ fontSize:32 }}>🤖</span>
    </motion.button>
  );
};

export default ChatbotButton;


