import React, { useState } from "react";
import emailjs from "emailjs-com";
const SERVICE = "default_service";
const TEMPLATE = "template_portfolio";
const USERID = "user_default";

const ContactForm = () => {
  const [sent, setSent] = useState(false);
  const [loading, setLoading] = useState(false);
  const [err, setErr] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setErr("");
    try {
      await emailjs.sendForm(SERVICE, TEMPLATE, e.target, USERID);
      setSent(true);
      setLoading(false);
      e.target.reset();
    } catch (err) {
      setErr("Ошибка отправки, попробуйте позже.");
      setLoading(false);
    }
  };

  if (sent) return (
    <section>
      <h2 className="section-title">Спасибо! Ваша заявка получена.</h2>
      <div className="glass-card" style={{ margin: "auto", maxWidth: 350, padding: 24, textAlign: "center" }}>
        Я свяжусь с вами в ближайшее время!
      </div>
    </section>
  );

  return (
    <section id="contact">
      <h2 className="section-title">Связаться со мной</h2>
      <form onSubmit={handleSubmit}
        className="glass-card"
        style={{
          margin: "auto",
          maxWidth: 450,
          padding: 32,
          display: "grid",
          gap: "1.1rem"
        }}>
        <input name="name" type="text" required placeholder="Ваше имя" />
        <input name="email" type="email" required placeholder="E-mail" />
        <textarea name="message" required placeholder="Ваше сообщение" rows="4" />
        {err && <div style={{ color: "#e66", fontSize: "0.93rem" }}>{err}</div>}
        <button type="submit" className="btn" disabled={loading}>
          {loading ? "Отправка..." : "Отправить"}
        </button>
      </form>
    </section>
  );
};

export default ContactForm;
